.favoriteHeader{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  .title {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 32px;
    letter-spacing: 0.16em;
    text-transform: uppercase;
    margin: 40px 0;
  }

  .cleanText {
    cursor: pointer;
    opacity: 0.6;
    font-size: 14px;
    letter-spacing: 0.02em;
    margin-right: 50px;

    img {
      padding-right: 5px;
      position: relative;
      top: 3px;
    }
  }

}

.items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.textFavoriteIsClean {
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 30px;
  letter-spacing: 0.16em;
  text-transform: uppercase;
}


